@charset "UTF-8";
/* ----- App.js  ----- */
body {
  background: url("../img/bg.jpg") center center fixed no-repeat;
  background-size: cover;
  padding: 0px;
  margin: 0px; }

/*-- 因應 <Link /> 使用 --*/
a {
  text-decoration: none;
  color: inherit; }

/* ----- Nav.js  ----- */
.window-UnMoving {
  background-color: transparent;
  position: fixed;
  top: 0;
  height: 46px;
  transition: 0.5s all;
  width: 100%;
  z-index: 1;
  display: flex;
  align-items: center; }
  .window-UnMoving ul {
    padding: 0;
    list-style: none; }
    .window-UnMoving ul li {
      font-size: 16px;
      color: white;
      font-weight: normal;
      font-family: monospace;
      text-align: center;
      outline: none;
      display: inline-block;
      padding: 12px; }
      .window-UnMoving ul li ~ li {
        cursor: pointer;
        margin-left: 8px; }
    .window-UnMoving ul li.active {
      transition: 0.5s all;
      background-color: #eba500;
      box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
    .window-UnMoving ul .date-info {
      position: absolute;
      right: 15px; }
      .window-UnMoving ul .date-info li {
        cursor: text;
        font-size: 16px;
        color: white;
        font-weight: normal;
        font-family: monospace; }

.window-is-Moving {
  background-color: #eba500;
  position: fixed;
  top: 0;
  height: 46px;
  width: 100%;
  transition: 0.5s all;
  z-index: 1;
  box-shadow: 0px 4px 3px rgba(51, 51, 102, 0.5);
  display: flex;
  align-items: center; }
  .window-is-Moving ul {
    list-style: none;
    padding: 0; }
    .window-is-Moving ul li {
      font-size: 16px;
      color: white;
      font-weight: normal;
      font-family: monospace;
      text-align: center;
      outline: none;
      display: inline-block;
      padding: 12px; }
      .window-is-Moving ul li ~ li {
        cursor: pointer;
        margin-left: 8px; }
    .window-is-Moving ul li.active {
      transition: 0.5s all;
      color: #0074bd; }
    .window-is-Moving ul .date-info {
      position: absolute;
      right: 15px; }
      .window-is-Moving ul .date-info li {
        cursor: text;
        font-size: 16px;
        color: white;
        font-weight: normal;
        font-family: monospace; }

/* ----- Home.js  ----- */
.weather {
  display: flex;
  position: relative;
  height: auto;
  width: 800px;
  transition: 0.3s all;
  margin: 20px; }
  .weather .weather-card {
    height: auto;
    width: 300px;
    padding: 30px;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5);
    background-color: rgba(197, 188, 168, 0.1);
    border: 3px solid wheat;
    margin: 20px; }
    .weather .weather-card h1 {
      font-size: 40px;
      color: white;
      font-weight: bold;
      font-family: monospace; }
    .weather .weather-card p {
      font-size: 16px;
      color: white;
      font-weight: normal;
      font-family: monospace;
      margin-bottom: 10px; }

.page-title {
  font-size: 80px;
  color: white;
  font-family: Impact, Charcoal, sans-serif;
  position: relative;
  height: auto;
  width: 800px;
  border-bottom: 3px solid wheat;
  border-right: 3px solid wheat;
  margin: 20px;
  padding: 10px;
  box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }

/* ----- PostList.js / Home.js  ----- */
.loading {
  font-size: 20px;
  color: white;
  font-family: monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 150px;
  width: 150px;
  margin: 20px auto;
  padding: 70px; }

.wrapper {
  margin: 0 auto;
  min-height: 900px;
  height: auto;
  width: auto;
  padding: 30px; }
  .wrapper .board {
    height: auto;
    width: 1000px;
    background-color: rgba(110, 105, 105, 0.35);
    margin: 100px auto;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
    .wrapper .board .post {
      cursor: pointer;
      position: relative;
      width: 750px;
      border: 3px solid wheat;
      transition: 0.25s all;
      margin: 20px;
      padding: 30px;
      box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
      .wrapper .board .post:hover {
        border-color: #eba500;
        background-color: rgba(235, 165, 0, 0.2); }
      .wrapper .board .post-title {
        font-size: 20px;
        color: white;
        font-weight: bold;
        font-family: monospace;
        padding: 5px 8px;
        height: 30px;
        word-break: break-word;
        text-overflow: ellipsis;
        overflow: hidden;
        transition: 0.3s all;
        white-space: nowrap; }
      .wrapper .board .post-text {
        font-size: 13px;
        color: white;
        font-family: monospace;
        line-height: 1.4em;
        padding: 5px 8px;
        margin-bottom: 40px;
        height: 91px;
        overflow: hidden;
        text-overflow: ellipsis; }
        .wrapper .board .post-text:hover {
          color: white; }
      .wrapper .board .post-editor {
        font-size: 13px;
        color: white;
        font-weight: bold;
        font-family: monospace;
        bottom: 25px;
        right: 25px;
        padding: 5px 8px;
        width: 180px;
        word-break: break-word;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden; }

/* -----  About.js ----- */
.info-page {
  background-color: rgba(37, 33, 22, 0.1);
  position: relative;
  height: auto;
  width: auto;
  min-width: 700px;
  border: 3px solid wheat;
