/* ----- Post.js  ----- */
.single-post {
  background-color: rgba(37, 33, 22, 0.1);
  position: relative;
  height: auto;
  width: auto;
  min-width: 700px;
  border: 3px solid wheat;
  transition: 0.3s all;
  margin: 20px;
  padding: 60px;
  padding-bottom: 140px;
  box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
  .single-post:hover {
    background-color: rgba(37, 33, 22, 0.8); }
  .single-post-title {
    font-size: 40px;
    color: wheat;
    font-weight: bold;
    font-family: monospace;
    padding: 5px 8px;
    height: auto;
    word-break: break-word;
    transition: 0.3s all; }
  .single-post-text {
    font-size: 14px;
    color: #d8d8d8;
    font-family: monospace;
    margin-top: 50px;
    padding: 5px 8px;
    height: auto;
    word-break: break-word;
    line-height: 2em; }
    .single-post-text p img {
      max-width: 700px; }
    .single-post-text p a {
      color: wheat;
      padding-bottom: 3px;
      border-bottom: 1.5px solid #eba500; }
    .single-post-text pre {
      box-shadow: 0px 0px 15px black;
