      overflow: auto;
      border: 1px solid wheat;
      border-radius: 4px; }
    .single-post-text blockquote {
      font-size: 18px;
      color: wheat;
      font-weight: bolder;
      font-family: monospace; }
  .single-post-editor {
    font-size: 20px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    position: absolute;
    bottom: 45px;
    right: 40px;
    padding: 5px 8px;
    word-break: break-word; }
  .single-post-editblock {
    font-size: 14px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    position: absolute;
    bottom: 45px;
    left: 60px;
    padding: 5px 8px;
    word-break: break-word; }
    .single-post-editblock-edit, .single-post-editblock-delete {
      cursor: pointer;
      padding: 12px 20px;
      border: 2px solid wheat;
      transition: 0.2s all;
      box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
      .single-post-editblock-edit:hover, .single-post-editblock-delete:hover {
        color: #0074bd;
        background-color: #eba500; }
    .single-post-editblock-delete {
      margin: 25px; }

.edit-article {
  height: auto;
  transition: 0.3s all;
  display: flex;
  justify-content: center;
  flex-direction: column; }
  .edit-article-title {
    font-size: 40px;
